  import mixins from './mpother'
  import mp from './mp'
  export default {
	  name:'UniSwipeAction',

    mixins: [mp],




    props: {
      /**
       * 按钮内容
       */
      options: {
        type: Array,
        default () {
          return []
        }
      },
      /**
       * 禁用
       */
      disabled: {
        type: Boolean,
        default: false
      },
      /**
       * 变量控制开关
       */
      show: {
        type: Boolean,
        default: false
      },
      /**
       * 是否自动关闭
       */
      autoClose: {
        type: Boolean,
        default: true
      }
    }
  }
